/* eslint-disable @typescript-eslint/no-unused-expressions */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import * as _ from 'lodash';
import * as fromAuth from 'store/auth/shared/selectors';
import logo from 'img/logo-removebg-preview.png';
import moment from 'moment';
import { CurrencyService } from 'services/currency.service';
import { useSelector } from 'react-redux';

type Props = {
  info: any;
  startDate: any;
  endDate: any;
  adminView: boolean;
  typeCard: string;
};

export const PdfReturn = React.forwardRef<HTMLDivElement, Props>(
  ({ info, startDate, endDate, adminView, typeCard }, ref) => {
    if (_.isEmpty(info) && _.isEmpty(startDate) && _.isEmpty(endDate))
      return <></>;
    const user: any = useSelector<any>(fromAuth.selectUser);
    const renderHeaDer = () => {
      return (
        <header>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <div style={{ width: '120px', height: '120px' }}>
              <img src={logo} alt={logo} />
            </div>
            <div style={{ fontSize: '15px' }}>
              <p> TEKMEDI JSC </p>
              <p>
                {' '}
                The Sun Avenue SAV8 - 22.08, S 28, ng Mai Ch Th, Phng An
                Ph, Qun 2, Thnh ph H Ch Minh{' '}
              </p>
              <p>
                <span style={{ marginRight: '5px' }}> www.tekmedi.com </span>
                <span style={{ marginRight: '5px' }}>
                  service @tekmedi.com{' '}
                </span>{' '}
                <span style={{ marginRight: '5px' }}>(028) 3 930 1439 </span>
              </p>
            </div>
          </div>
          <div style={{ textAlign: 'center' }}>
            <p style={{ fontWeight: 'bold' }}> THNG K CHI TIT TR TH</p>
          </div>
          <div style={{ textAlign: 'left' }}>
            <p style={{ fontWeight: 'bold' }}> Thnng tin giao dch </p>
          </div>
          <div>
            <p>
              {' '}
              T ngy: {moment(startDate).format('DD-MM-YYYY')} n ngy{' '}
              {moment(endDate).format('DD-MM-YYYY')}
            </p>
          </div>
        </header>
      );
    };

    const renderAdminView = () => {
      if (adminView && typeCard !== '2') {
        return (
          <table className="tablePdf-admin">
            <thead>
              <tr className="tag-td-admin">
                <th colSpan={6}>Tng s nhn vin</th>
                <th colSpan={5}>Tng s ln thc hin</th>
                <th colSpan={5}>Tng s Bnh nhn</th>
                <th colSpan={6}>Tng s tin tr th</th>
              </tr>
            </thead>
            <tbody>
              <tr className="tag-td-data">
                <td colSpan={6}>
                  {CurrencyService.formatNumber(info.total_employee)}
                </td>
                <td colSpan={5}>
                  {CurrencyService.formatNumber(info.total_perform)}
                </td>
                <td colSpan={5}>
                  {CurrencyService.formatNumber(info.total_patient)}
                </td>
                <td colSpan={6} className="font-weight-bold">
                  {CurrencyService.formatCurrency(info.total_price)}
                </td>
              </tr>
            </tbody>
          </table>
        );
      }
      if (adminView && typeCard === '2') {
        return (
          <table className="tablePdf-admin">
            <thead>
              <tr className="tag-td-admin">
                <th colSpan={3}>Tng s nhn vin</th>
                <th colSpan={3}>Tng s ln thc hin</th>
                <th colSpan={3}>Tng s Bnh nhn</th>
                <th colSpan={8}>Tng s chi tit loi hnh</th>
                <th colSpan={6}>Tng s tin tr th</th>
              </tr>
            </thead>
            <tbody>
              <tr className="tag-td-data">
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_employee)}
                </td>
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_perform)}
                </td>
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_patient)}
                </td>
                <td colSpan={2}>Tin mt</td>
                <td colSpan={2}>
                  {CurrencyService.formatNumber(info.total_card_scan)}
                </td>
                <td rowSpan={2} colSpan={6} className="font-weight-bold">
                  {CurrencyService.formatCurrency(info.total_price)}
                </td>
              </tr>
              <tr>
                <td colSpan={2}>Chuyn khon</td>
                <td colSpan={2}>
                  {CurrencyService.formatNumber(info.total_transfer)}
                </td>
              </tr>
            </tbody>
          </table>
        );
      }
    };

    const renderTable = () => {
      return info.data_groups.map((row, idx) => {
        return (
          <tbody key={idx}>
            {adminView ? (
              <tr>
                <td colSpan={19} className="text-left font-weight-bold">
                  {row.employee_name}
                </td>
                <td colSpan={3} className="font-weight-bold">
                  {CurrencyService.formatCurrency(row.total_price_employee)}
                </td>
              </tr>
            ) : (
              <tr>
                <td colSpan={22} className="text-left font-weight-bold">
                  {row.employee_name}
                </td>
              </tr>
            )}

            {row.datas.map((item, id) => {
              return (
                <tr key={id}>
                  <td colSpan={2}>{item.row_num}</td>
                  <td colSpan={4}>{item.tekmedi_card_number}</td>
                  <td colSpan={4}>{item.patient_code}</td>
                  <td colSpan={3}>{item.patient_name}</td>
                  <td colSpan={3}>{item.time}</td>
                  <td colSpan={3}>{item.payment_type_name}</td>
                  <td colSpan={3}>
                    {CurrencyService.formatCurrency(item.price)}
                  </td>
                </tr>
              );
            })}
          </tbody>
        );
      });
    };

    const renderTFoot = () => {
      if (info.data_groups.length) {
        return (
          <tbody>
            <tr>
              <td colSpan={19} className="text-right font-weight-bold">
                Tng tin
              </td>
              <td colSpan={3} className="font-weight-bold">
                {CurrencyService.formatCurrency(info.total_price)}
              </td>
            </tr>
          </tbody>
        );
      }
    };

    const renderFooter = () => {
      return (
        <div
          style={{
            float: 'right',
            width: '450px',
            textAlign: 'center',
            marginTop: '50px',
          }}
        >
          <p>
            {' '}
            Bnh vin Ch Ry, {moment().format('HH')} gi{' '}
            {moment().format('mm')} pht , Ngy {moment().format('DD')} thng{' '}
            {moment().format('MM')} nm {moment().format('YYYY')}
          </p>
          <p style={{ marginTop: '50px' }}>
            {!_.isEmpty(user) ? user.full_name : ''}
          </p>
        </div>
      );
    };

    return (
      <div ref={ref} {...info} style={{ pageBreakAfter: 'always' }}>
        {renderHeaDer()}
        {renderAdminView()}
        <table className="tablePdf">
          <thead>
            <tr className="tag-td-user">
              <td colSpan={2}>S phiu</td>
              <td colSpan={4}>M th BN</td>
              <td colSpan={4}>M bnh nhn</td>
              <td colSpan={3}>Tn bnh nhn</td>
              <td colSpan={3}>Ngy gi</td>
              <td colSpan={3}>Loi hnh</td>
              <td colSpan={3}>S tin</td>
            </tr>
          </thead>
          {renderTable()}
          {renderTFoot()}
        </table>
        {renderFooter()}
      </div>
    );
  },
);
